.clip__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  /* position: relative; */

  margin: 5px 10px;
  height: 500px;
  width: 300px;
  background-color: bisque;
  border-radius: 5px;

}

.clip__title {
  height: 70px;
  width: 90%;
  margin: 5px 0;
  /* background-color: inherit; */
  border-radius: inherit;
  overflow: hidden;
  text-overflow: ellipsis;
}

.clip__picture__container {
  height: 170px;
  width: 100%;
  background-size: cover;
  overflow: hidden;
  margin: 0;
}

.clip__picture {
  object-fit: contain;
  overflow: hidden;
}

.clip__info {
  height: 30px;
  margin: 5 auto;
}

