}

.clip__info_text {
}

.channel__name {
}

.upload__date {
}

.view__count {
}

.clip__description {
  height: 125px;
  width: 96%;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 5px auto;
  margin-top: 10px;
}
