/* main part */
body {
  background-color: rgb(47, 56, 47);
}

input:focus, button:focus {
  outline: none;
}

a:active,
a:hover,
a:visited,
a {

  text-decoration: none;
}

.main__container {
  width: 100%;
}

/* searchbox part */
.search__box__container {
  width: 100%;
  text-align: center;
}

.search__box {
  width: 80%;
  height: 30px;

  padding: 0 5%;

  margin: 10px 0;
  border-radius: 30px;
  font-family: Arial, sans-serif;
  font-size: 20px;
  font-style: italic;
  color: darkgreen;
  background-color: bisque;
}

/* nav part */
.nav__container {
  display: flex;
  justify-content: center;
}

.nav__container button {
  height: 40px;
  width: 40px;

  margin: 10px 15px;
  border-radius: 50%;
  background-color: bisque;
  box-shadow: none;
}

.prev__pop, .next__pop {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 40px;
  width: 40px;

  margin: 10px 15px;
  border-radius: 5%;

  background-color: lightgreen;
  box-shadow: none;
}

/* clips part */
.clip__section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider__box {
  display: flex;
  align-items: center;

  width: 1280px;
  height: 500px;

  overflow: hidden;
  scroll-behavior: smooth;
}


@media (max-width: 1280px) {
  .slider__box{width: 960px;}
}

@media (max-width: 960px) {
  .slider__box{width: 640px;}
}

@media (max-width: 640px) {
  .slider__box{width: 320px;}
}

.hidden {
  visibility: hidden;
}

.grab {
  cursor: grabbing;
}
